/* -- INPUT AREA ------------------*/

/* ------------------ MAIN ------------------*/
.lightmode {
  background-color: #e9e9e9;
}
.darkmode {
  background-color: #292929;
}
.main-container {
  text-align: center;
  max-width: 750px;
  margin: 0 auto;
}
.darkmode .main-container {
  background-color: #2b3047;
}
.lightmode .main-container {
  background-color: #f3f3f5;
}
.main-container {
  height: 100vh;
}
button {
  outline: none;
}
button:hover {
  cursor: pointer;
}
/* ------------------ HEADER ------------------*/
.header-container {
  height: 10vh;
  min-height: 50px;
  color: #fff;
  position: fixed;
  width: 100%;
  max-width: 750px;
  top: 0;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}
.darkmode .header-container {
  background-color: #1a1e32;
}
.lightmode .header-container {
  background-color: #ff0545;
}
.header-menu {
  float: right;
  margin-top: 15px;
}
.header-title {
  float: left;
  margin-top: 15px;
  margin-left: 5px;
}
.header-title img {
  width: 80px;
}
.header-btn {
  width: 40px;
  height: 40px;
  margin-top: 15px;
  margin-right: 5px;
  border: none;
  color: #fff;
  border-radius: 5px;
  font-size: 1.1em;
  background: transparent;
}
/* .lightmode .header-btn {
  background: #da0038;
}
.darkmode .header-btn {
  background: #2b3047;
} */
div[role="menu"] {
  visibility: hidden;
}

div[role="menu"].visible {
  visibility: visible;
  transform: translateY(-0.3rem);
  transition: transform 0.2s, opacity 0.2s, visibility linear;

  opacity: 1;
}
div[role="menu"] {
  background: #fff;
  padding: 0.8rem 1rem;
  border: 1px solid #ddd;
  border-radius: 0.25rem;
  box-shadow: 0 0.2rem 0.4rem rgb(0 0 0 / 10%);
  opacity: 0;
  visibility: hidden;
  transition: transform 0.2s, opacity 0.2s, visibility 0s linear 0.2s;
  will-change: transform;
  position: absolute;
  width: fit-content;
  top: calc(100% + (-15px));
  right: 15px;
  margin: 0.8rem auto auto;
  z-index: 2;
  text-align: left;
}
div[role="menu"] a {
  display: block;
  font-size: 1rem;
  padding: 0.25rem;
  color: #29779e;
  text-decoration: none;
  cursor: pointer;
  outline: 0;
  border: 0.1rem solid transparent;
  border-radius: 0.2rem;
  user-select: none;
}
.menuoption {
  display: inline-flex;
  top: -5px;
  position: relative;
  margin-left: 20px;
}
.divider {
  border-top: 1px solid #ebebec;
  margin: 5px -17px;
}
/* ------------------ CHAT AREA ------------------*/
.chatarea-container {
  padding: 10px;
  height: 75vh;
  margin: 10vh 0;
  overflow-y: scroll;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
}
.chatarea-container::-webkit-scrollbar {
  width: 0.25rem;
}

.lightmode .chatarea-container::-webkit-scrollbar-track {
  background: #cccccc;
}

.chatarea-container::-webkit-scrollbar-thumb {
  background: #ff0545;
}
.darkmode .chatarea-container::-webkit-scrollbar-track {
  background: #1e1e24;
}

.darkmode .chatarea-container::-webkit-scrollbar-thumb {
  background: #6649b8;
}
.messagearea-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  min-height: 100vh;
}
.darkmode .textdisplay {
  color: #fff;
}
.sent {
  flex-direction: row-reverse;
}
.sent p {
  color: #fff;
  -webkit-align-self: flex-end;
  align-self: flex-end;
}
.darkmode .sent p {
  background: #ff3662;
}
.darkmode .received p {
  background: #1a1e32;
}
.lightmode .sent p {
  background: #ff0545;
}
.lightmode .received p {
  background: #e1e1e1;
  color: #181717;
}
.received p {
  margin-left: 5px;
}
.sent p {
  margin-right: 5px;
}
.textdisplay {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
}
.userimg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}
p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 10px;
  position: relative;
  color: #fff;
  text-align: center;
}
.signin-btn {
  margin: 0 auto;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 5px;
  background-color: #ff0545;
}
/* ------------------ INPUT AREA ------------------*/
form {
  height: 10vh;
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 750px;
  display: -webkit-flex;
  display: flex;
  font-size: 1.25rem;
}
.darkmode .messagearea-container {
  background: #2b3047;
}
.lightmode .messagearea-container {
  background: #f3f3f5;
}
.messagearea-container form {
  width: 100%;
}
.messagearea-container input {
  height: 40px;
  border-radius: 20em;
  width: 85%;
  font-size: 1.2rem;
  outline: none;
  float: left;
  margin: 0 10px;
  padding: 10px;
}
.darkmode .messagearea-container input {
  background: #1a1e32;
  color: #fff;
}
.lightmode .messagearea-container input {
  background: #ffffff;
  color: #181717;
  border-color: #bcbcbc;
}
.sendmsg-btn {
  width: 60px;
  height: 60px;
  border-radius: 100px;
  outline: none;
  margin-right: 10px;
  color: #fff;
}
.darkmode .primary-btn {
  background-color: #ff3662;
}
.lightmode .primary-btn {
  background-color: #ff0545;
}

.react-confirm-alert-overlay.darkmode {
  background: rgb(30 30 30 / 90%);
}
